import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SelfserviceComponent } from './components/selfservice.component';
import { HolidayDetailsComponent } from './components/holiday-details/holiday-details.component';
import { ChangepasswordComponent } from './components/changepassword/changepassword.component';


const routes: Routes = [
    {
        path: '', component: SelfserviceComponent,
        children: [
            { path: 'myprofile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\myprofile\\myprofile.module')['MyprofileModule']);  });}) },
            { path: 'mysalary', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\mysalary\\mysalary.module')['MysalaryModule']);  });}) },
            { path: 'myleaves', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\myleaves\\myleaves.module')['MyleavesModule']);  });}) },
            { path: 'myadvancesclaim', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\myadvancesclaim\\myadvancesclaim.module')['MyadvancesclaimModule']);  });}) },
            { path: 'myattendance', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\myattendance\\myattendance.module')['MyattendanceModule']);  });}) },
        ]
    },
    { path: 'holidaysdetails', component: HolidayDetailsComponent },
    { path: 'changepassword', component: ChangepasswordComponent },
    //{ path: 'lists/:lookup', component: LookupListsComponent },
    //{ path: 'form/:lookup', component: FormComponent },
    //{ path: 'form/:lookup/:rowId', component: FormComponent },
    //{ path: 'delete/:lookup/:rowId', component: DeleteComponent },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class SelfserviceRoutingModule { }



